<template>
  <v-card
    height="100%"
  >
    <div
      id="docentes-preuniversitarios-dedicacion"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Dedicación.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('docentes-preuniversitarios-dedicacion')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as docentes preuniversitarios.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="BarChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Dedicación
                </th>
                <th class="text-left">
                  Cargos
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in categoriaData"
                :key="index"
              >
                <td>{{ parseDedicacion(index) }}</td>
                <td>{{ item }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>
<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['preuniversitarios', 'mobileSize']),
    barChartOptions () {
      return {
        chartArea: {
          left: 140,
          right: 20,
          top: 20,
          bottom: 20
        },
        // tooltip: {
        //   isHtml: true,
        //   textStyle: { color: 'white', bold: false }
        // },
        colors: this.colors,
        title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        legend: {
          position: 'none',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'

          }
        },
        bar: { groupWidth: '70%' },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        hAxis: {
          format: '0',
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    barChartData () {
      return {
        cols: [
          { id: 'Dedicacion', label: 'Cargos', type: 'string' },
          { id: 'Dedicacion', label: 'Cargos', type: 'number' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: '12 HORAS' },
              { v: this.categoriaData.doce, f: this.categoriaData.doce }
            ]
          },
          {
            c: [
              { v: 0, f: '14 HORAS' },
              { v: this.categoriaData.catorce, f: this.categoriaData.catorce }
            ]
          },
          {
            c: [
              { v: 0, f: '15 HORAS' },
              { v: this.categoriaData.quince, f: this.categoriaData.quince }
            ]
          },
          {
            c: [
              { v: 0, f: '16 HORAS' },
              { v: this.categoriaData.dieciseis, f: this.categoriaData.dieciseis }
            ]
          },
          {
            c: [
              { v: 0, f: '18 HORAS' },
              { v: this.categoriaData.dieciocho, f: this.categoriaData.dieciocho }
            ]
          },
          {
            c: [
              { v: 0, f: '20 HORAS' },
              { v: this.categoriaData.veinte, f: this.categoriaData.veinte }
            ]
          },
          {
            c: [
              { v: 0, f: '25 HORAS' },
              { v: this.categoriaData.veinticinco, f: this.categoriaData.veinticinco }
            ]
          },
          {
            c: [
              { v: 0, f: '30 HORAS' },
              { v: this.categoriaData.treinta, f: this.categoriaData.treinta }
            ]
          },
          {
            c: [
              { v: 0, f: '40 HORAS' },
              { v: this.categoriaData.cuarenta, f: this.categoriaData.cuarenta }
            ]
          },
          {
            c: [
              { v: 0, f: 'NIVEL MEDIO' },
              { v: this.categoriaData.nivelmedio, f: this.categoriaData.nivelmedio }
            ]
          },
          {
            c: [
              { v: 0, f: 'Sin Dedicacion' },
              { v: this.categoriaData.sindedicacion, f: this.categoriaData.sindedicacion }
            ]
          }
        ]
      }
    },
    categoriaData () {
      // "12 HORAS"
      // "14 HORAS"
      // "15 HORAS"
      // "16 HORAS"
      // "18 HORAS"
      // "20 HORAS"
      // "25 HORAS"
      // "30 HORAS"
      // "40 HORAS"
      // "NIVEL MEDIO"
      // "Sin Dedicacion"
      return this.preuniversitarios
        .reduce(
          (acc, cur) => {
            const doc = cur.descripcion_dedicacion === '12 HORAS'
            const cat = cur.descripcion_dedicacion === '14 HORAS'
            const qui = cur.descripcion_dedicacion === '15 HORAS'
            const dseis = cur.descripcion_dedicacion === '16 HORAS'
            const docho = cur.descripcion_dedicacion === '18 HORAS'
            const vei = cur.descripcion_dedicacion === '20 HORAS'
            const vcin = cur.descripcion_dedicacion === '25 HORAS'
            const tre = cur.descripcion_dedicacion === '30 HORAS'
            const cua = cur.descripcion_dedicacion === '40 HORAS'
            const nmed = cur.descripcion_dedicacion === 'NIVEL MEDIO'
            const sin = cur.descripcion_dedicacion === 'Sin Dedicacion'
            return doc
              ? { ...acc, doce: acc.doce + 1 }
              : cat
                ? { ...acc, catorce: acc.catorce + 1 }
                : qui
                  ? { ...acc, quince: acc.quince + 1 }
                  : dseis
                    ? { ...acc, dieciseis: acc.dieciseis + 1 }
                    : docho
                      ? { ...acc, dieciocho: acc.dieciocho + 1 }
                      : vei
                        ? { ...acc, veinte: acc.veinte + 1 }
                        : vcin
                          ? { ...acc, veinticinco: acc.veinticinco + 1 }
                          : tre
                            ? { ...acc, treinta: acc.treinta + 1 }
                            : cua
                              ? { ...acc, cuarenta: acc.cuarenta + 1 }
                              : nmed
                                ? { ...acc, nivelmedio: acc.nivelmedio + 1 }
                                : sin
                                  ? { ...acc, sindedicacion: acc.sindedicacion + 1 }
                                  : acc
          },
          {
            doce: 0,
            catorce: 0,
            quince: 0,
            dieciseis: 0,
            dieciocho: 0,
            veinte: 0,
            veinticinco: 0,
            treinta: 0,
            cuarenta: 0,
            nivelmedio: 0,
            sindedicacion: 0
          }
        )
    }
  },
  methods: {
    parseDedicacion (v) {
      return v === 'doce'
        ? '12 HORAS'
        : v === 'catorce'
          ? '14 HORAS'
          : v === 'quince'
            ? '15 HORAS'
            : v === 'dieciseis'
              ? '16 HORAS'
              : v === 'dieciocho'
                ? '18 HORAS'
                : v === 'veinte'
                  ? '20 HORAS'
                  : v === 'veinticinco'
                    ? '25 HORAS'
                    : v === 'treinta'
                      ? '30 HORAS'
                      : v === 'cuarenta'
                        ? '40 HORAS'
                        : v === 'nivelmedio'
                          ? 'NIVEL MEDIO'
                          : v === 'sindedicacion'
                            ? 'Sin Dedicacion'
                            : v
    }
  }
}
</script>
